import { Popup } from './popup';

export class MfPopup extends Popup {
    openPopup() {
        $.magnificPopup.open({
            showCloseBtn: false,
            type: 'inline',
            tLoading: '...',
            items: {
                src: this.popupId
            }
        });
    }

    closePopup() {
        $.magnificPopup.close();
    }
}


